ou may wonder why I did not eat ramen! This simple answer is I did not really understand how easy it was to make and how edible it was! I grew up believing ramen should be eaten under no circumstances because of the amount of salt and other nonsense they threw into it. ")]),_c('p',[_vm._v(" What did I do? I lost weight. Turns out the rice and pizza diet is fairly effective at keeping BMI low! My other go to was finding free food. Free pizza on campus? I am there. Spending every Sunday lunch at a friend’s house? Consider me adopted. The Monday night Bible study provides supper? My stomach and I are in the Lord’s house. I got by. ")]),_c('p',[_vm._v(" Eventually I had more time and more money. Back to higher quality food I went! But until that was the case, rice and oregano were my friends. ")])])])])])}]

